import Component from '../react/component'

const ReactDOM = {
  render
}

// ÊåÇËΩΩ DOM
function render(vnode, container) {
  container.appendChild(_render(vnode))
}

// Ê†πÊçÆËôöÊãü„ÄÄDOM„ÄÄÂàõÂª∫ÁúüÂÆû„ÄÄDOM
function _render(vnode) {
  if (vnode === undefined) return;

  // Â≠óÁ¨¶‰∏≤ÂàõÂª∫ÊñáÊú¨ËäÇÁÇπ
  if (typeof vnode === 'string') return document.createTextNode(vnode);

  // tag ‰∏∫ÂáΩÊï∞ÂàôÂàõÂª∫ÁªÑ‰ª∂
  if (typeof vnode.tag === 'function') {
    // 1. ÂàõÂª∫ÁªÑ‰ª∂
    const comp = createComponent(vnode.tag, vnode.attrs);
    // 2. ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
    setComponentProps(comp, vnode.attrs);
    // 3. ÁªÑ‰ª∂Ê∏≤ÊüìÁöÑËäÇÁÇπÂØπË±°ËøîÂõû
    // return comp.base;
  }

  // ËôöÊãü DOM ÂØπË±°
  const { tag, attrs, childrens } = vnode;
    // ÂàõÂª∫ËäÇÁÇπÂØπË±°
  const dom = document.createElement(tag);

  // ÊúâÂ±ûÊÄß
  if (attrs) {
    Object.keys(attrs).forEach(key => {
      const value = attrs[key];
      setAttribute(dom, key, value);
    })
  }

  // ÊúâÂ≠êËäÇÁÇπÈÄíÂΩíÊ∏≤Êüì
  if (childrens) {
    childrens.forEach(child => {
      render(child, dom)
    })
  }

  return dom;
}

// ËÆæÁΩÆÂ±ûÊÄß(className style Á≠âÂ§ÑÁêÜ)
function setAttribute(dom, key, value) {
  // Â∞ÜÂ±ûÊÄßÂêç className ËΩ¨Êàê class
  if (key === 'className') {
    key = 'class';
    dom.setAttribute(key, value);
  }

  // Â¶ÇÊûúÊòØ‰∫ã‰ª∂
  if (/on\w+/.test(key)) {
    key = key.toLowerCase();
    dom.setAttribute(key, value);
  }

  // Â¶ÇÊûúÊòØÊ†∑Âºè
  if (key === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (value && typeof value === 'object') {
      for (let k in value) {
        if (typeof value[k] === 'number') {
          dom.style[k] = value[k] + 'px'
        } else {
          dom.style[k] = value[k];
        }
      }
    }
  } else {
    if (key in dom) {
      dom[key] = value || '';
    }
    if (value) {
      dom.setAttribute(key, value);
    } else {
      dom.removeAttribute(key);
    }
  }
}

// ÂàõÂª∫ÁªÑ‰ª∂
function createComponent(comp, props) {
  let inst;

  // Â¶ÇÊûúÊòØÁ±ªÂÆö‰πâÁöÑÁªÑ‰ª∂ÔºåÂàôÂàõÂª∫ÂÆû‰æãËøîÂõû
  if (comp.prototype && comp.prototype.render) {
    inst = new comp(props);
  } else { // ÂáΩÊï∞ÁªÑ‰ª∂ËΩ¨Êç¢ÊàêÁ±ªÁªÑ‰ª∂
    inst = new Component(props);
    inst.constructor = comp;
  }

  return inst;
}

export default ReactDOM;